table
{
	border: 1px solid #d6d6d6;
	-webkit-box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
	        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
	width:100%;
	margin-bottom: 20px;
	overflow: scroll;
}

th
{
	border:1px solid #d6d6d6;
	width:100px;
}

th, td
{
	padding:20px;
}

tr
{
	border: 1px solid #d6d6d6;
}


